<template>
    <div class="content_table">
    <!-- 输入框 -->
      <el-row :gutter="0">
        <el-col :lg="({span: 3, offset: 0})" :xl='({span: 2, offset: 0})' :offset="1"><span class="content_span">债权账号：</span></el-col>
        <el-col :lg="4" :xl='({span: 4, offset:0,})' class="content_padd" ><el-input v-model="username" placeholder="输入身份证账号" class="content_input"></el-input></el-col>
        <el-col :lg="({span: 2, offset: 0})" class="content_padd2" :xl="({span: 1, offset:0,})"><span class="content_span">备注：</span></el-col>
        <el-col :lg="({span: 6, offset: 0})" :xl="4" class="content_padd1"><el-input v-model="interest" placeholder="非必填项" class="content_input2"></el-input></el-col>
        <el-col :lg="({span: 7, offset:1 })" :xl="({span: 3, offset: 1})" class=""><el-button type="primary" class="content_button" @click="handleClick()" round>生成邀请码</el-button></el-col>
      </el-row>
      <el-row class="content_title" :gutter="0">
        <el-col :lg="({span: 7, offset:0 })" :xl="({span: 3, offset: 0})" class="">
            <div class="home_header">操作日志</div>
        </el-col>
      </el-row>
    <!--  -->
    <!-- 表格 -->
      <el-row :gutter="0">
        <el-col :lg="({span: 20, offset:0 })" :xl="({span:22, offset: 0})" class="">
            <div class="content_t">
              <el-table
              class="hidden-lg-only"
              border
              height='790px'
              cell-class-name='content_agin'
              header-cell-class-name='content_tit'
              row-class-name='content_text'
							:data="inviteList"
							style="width: 100%;">
							<el-table-column
								prop="createdTime"
								label="邀请码生成时间"
								width="220">
								<template slot-scope="scope">
									<span type="text" size="small">{{dateFormat(scope.row.createdTime,"yyyy/MM/dd hh:mm:ss")}}</span>
								</template>
							</el-table-column>
							<el-table-column
								prop="inviteCode"
								label="邀请码"
								width="150">
							</el-table-column>
              <el-table-column
								prop="status"
								label="邀请码使用状态"
								width="150">
                <template slot-scope="scope">
									<span type="text" size="small">{{scope.row.status?'已使用':'未使用'}}</span>
								</template>
							</el-table-column>
							<el-table-column
								prop="idCard"
								label="绑定账号ID"
								width="220">
							</el-table-column>
							<el-table-column
							prop="realName"
							label="姓名"
							width="150">
							</el-table-column>
							<el-table-column
							prop="mobile"
							label="手机号"
							width="180">
							</el-table-column>
							<el-table-column
							prop="remark"
							label="备注"
              width='604'
							>
							</el-table-column>
							</el-table>

              <el-table
              class="hidden-xl-only"
              border
              height='520px'
              cell-class-name='content_agin'
              header-cell-class-name='content_tit'
              row-class-name='content_text'
							:data="inviteList"
							style="width: 100%">
							<el-table-column
								prop="createdTime"
								label="邀请码生成时间"
								width="220">
								<template slot-scope="scope">
									<span type="text" size="small">{{dateFormat(scope.row.createdTime,"yyyy/MM/dd hh:mm:ss")}}</span>
								</template>
							</el-table-column>
							<el-table-column
								prop="inviteCode"
								label="邀请码"
								width="150">
							</el-table-column>
              <el-table-column
								prop="status"
								label="邀请码使用状态"
								width="150">
                <template slot-scope="scope">
									<span type="text" size="small">{{scope.row.status?'已使用':'未使用'}}</span>
								</template>
							</el-table-column>
							<el-table-column
								prop="idCard"
								label="绑定账号id"
								width="220">
							</el-table-column>
							<el-table-column
							prop="realName"
							label="姓名"
							width="150">
							</el-table-column>
							<el-table-column
							prop="mobile"
							label="手机号"
							width="180">
							</el-table-column>
							<el-table-column
							prop="remark"
							label="备注"
              width='580'
							>
							</el-table-column>
							</el-table>

            </div>
        </el-col>
      </el-row>
  </div>
</template>
<script>
import 'element-ui/lib/theme-chalk/display.css';
export default {
    data(){
      return{
          inviteList:[],
          username:'',
          interest:''
      }
    },
    methods:{
        getContent() {
			  this.$axios.get(this.$store.state.api+'/pledgeOperate/inviteNum/inviteList')
			  .then(this.getHomeInfoSucc)
		  },
		  getHomeInfoSucc (res) {
        this.inviteList = res.data.data
      },
      handleClick(){
        var _this=this;
        var username = _this.username
        var interest = _this.interest
			   const idcard = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
			   if(idcard.test(username)){

				this.$axios.post(this.$store.state.api+'/pledgeOperate/inviteNum/isUsable',{
						"idCard":username,
						"remark":interest
				})
				.then(function(response){
					const code = response.data.code
					if(code==200){
						alert('邀请成功！')
						_this.username='',
						_this.interest='',
						_this.getContent();
					}else if(code ==400){
						alert('该用户已被邀请')
					}else{
            alert(' 该用户不存在！')
					}

				})
				.catch(function(error){
						//console.log(error)
					})
			   }else{
           alert('请输入正确的身份证号')
           	_this.username=''
						_this.interest=''
			   }
      },
        dateFormat(millionsTime, pattern){
          var d = new Date();
          d.setTime(millionsTime);
          var date = {
              "M+": d.getMonth() + 1,
              "d+": d.getDate(),
              "h+": d.getHours(),
              "m+": d.getMinutes(),
              "s+": d.getSeconds(),
              "q+": Math.floor((d.getMonth() + 3) / 3),
              "S+": d.getMilliseconds()
          };
          if (/(y+)/i.test(pattern)) {
              pattern = pattern.replace(RegExp.$1, (d.getFullYear() + '').substr(4 - RegExp.$1.length));
          }
          for (var k in date) {
              if (new RegExp("(" + k + ")").test(pattern)) {
                  pattern = pattern.replace(RegExp.$1, RegExp.$1.length == 1
                      ? date[k] : ("00" + date[k]).substr(("" + date[k]).length));
              }
          }
          return pattern;
        }
    },
    watch:{

    },
    mounted(){
        this.getContent()
    }


}
</script>

<style>
.el-col{
  /* background: #659865; */
  /* margin-left:20px; */
  padding: 0 0 !important;
}
.content_padd{
  margin-left: -60px;
}
.content_padd1{
  margin-left: -20px;
}
.content_padd2{
  margin-left: 30px;
}
.content_span{
  height: 25px;
  line-height: 54px;
  font-family: 'PingFangSC-Medium';
  font-size: 18px;
  color: #212C3C;
  letter-spacing: -0.43px;
}
.content_input{
  height: 54px;
  line-height: 54px;
}
.content_input2{
  height: 54px;
  line-height: 54px;
}
.content_button{
  width: 160px;
  height: 42px;
  margin-left: -45px;
  margin-top: 5px;
  font-family: 'PingFangSC-Medium';
  font-size: 18px;
  color: #FFFFFF;
  letter-spacing: -0.43px;
}
.home_header{
  font-family: 'PingFangSC-Medium';
  font-size: 18px;
  color: #212C3C;
  letter-spacing: -0.43px;
}
.content_title{
  margin-top: 17px;
}
.content_t{
  min-width: 950px;
  max-width: 1675px;
  margin-top: 8px;
  height: 872px;
}
.content_text{
  font-family: 'PingFangSC-Regular';
  font-size: 14px;
  color: #212C3C;
  letter-spacing: -0.34px;
  text-align: center !important;
}
.content_tit{
  font-family: 'PingFangSC-Regular';
  font-size: 16px;
  color: #212C3C;
  letter-spacing: -0.39px;
  background: #E4E8EE !important;
  text-align: center !important;
  border: 1px solid #B7C3D5;
}
.cell{
   text-align: center;
}
.content_agin{
  border: 1px solid #B7C3D5;
  height: 65px;
}

/* .cell{
  padding-right:40px !important;
  padding-left:40px !important;
} */
</style>



